/* If you need to add @import statements, do so up here */

@import "jit-refresh.css"; /* triggers frontend rebuilds */
@import "fonts.css"; /* load fonts */

/* Set up Tailwind imports */
@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --header-height: 80px;
  --lychee-color: #fef6eb;
  --grapefruit-color: #ffc5c4;
  --madang-color: #e1f3d5;
  --light-algae-color: #adcec2;
  --algae-color: #44a3a2;
  --spinach-color: #166565;
  --lemon-color: #ffb92b;
  --tangerine-color: #fc8300;
  --body-background: #fef6eb;
  --primary-color: #37717f;
  --secondary-color: #c6d8d9;
  --text-color: #1f1e1e;
}

html {
  @apply scroll-smooth;
}

body {
  @apply font-sans h-full min-h-screen flex flex-col overflow-y-scroll;
  background-color: var(--lychee-color);
}

a {
  color: var(--spinach-color);
  text-decoration: underline;
  text-decoration-color: var(--algae-color);
}

h1 {
  @apply font-serif font-bold text-3xl text-left w-fit pt-4 pb-8;
  color: var(--spinach-color);
}

h2 {
  @apply font-serif font-bold text-2xl py-8;
  color: var(--algae-color);
}

body > nav {
  @apply flex flex-row justify-center;
  height: var(--header-height);
}

body > nav ul {
  @apply flex flex-row items-center rounded-lg w-fit py-4 px-4 gap-4 my-8 text-3xl font-bold font-sans;
  color: var(--spinach-color);
}

body > nav a {
  text-decoration: none;
}

code {
  @apply text-base font-mono break-words overflow-y-scroll;
}

main {
  @apply w-full md:w-[600px] lg:w-[600px] px-2 pb-12 md:mx-auto lg:mx-auto text-xl;
  background: var(--lychee-color);
  color: var(--text-color);
  height: 100%;
  min-height: calc(100vh - var(--header-height));
}

p {
  @apply mt-6 leading-loose;
}

p code {
  @apply text-base text-black rounded-full overflow-auto px-2;
  /*color: var(--spinach-color);*/
  background-color: var(--light-algae-color);
}

p img {
  @apply mx-auto rounded-xl;
}

p img + em {
  @apply block text-center font-sans mx-auto text-sm;
  color: var(--tangerine-color);
}

main > ol, main > ul {
  @apply ml-6 mt-4;
}

main > ul > li, main > ol > li {
  @apply py-2;
}

main > ol {
  @apply list-decimal;
}

main > ul {
  @apply list-disc;
}

img {
  @apply rounded-xl my-4;
}

figure, img, video {
  @apply max-w-full text-center mx-auto my-8;
}

article figure {
  @apply w-auto max-w-full mx-auto;
}

blockquote {
  @apply mx-auto text-xl relative w-full md:w-[480px] lg:w-[480px] p-6 font-serif my-12 border border-t-4 rounded-b-3xl;
  border-color: var(--grapefruit-color);
  background-color: var(--grapefruit-color);
  color: var(--spinach-color);
}

blockquote p {
  @apply my-0;
}

dl {
  @apply py-1;
}

dt {
  @apply sr-only;
}

dd {
  @apply text-base leading-6 font-medium text-gray-500;
}

dd ul {
  @apply list-none flex flex-row justify-center content-center items-center mx-auto;
}

dd li {
  @apply mx-2 text-xs inline-flex items-center font-bold uppercase px-3 py-1 bg-green-200 text-green-700 rounded-full;
}

footer {
  text-align: center;
  margin-bottom: 4rem;
  font-size: 1em;
}

hr {
  /*@apply rounded-full h-4 w-96;*/
  /*background-color: var(--tangerine-color);*/
  /*margin: 3rem 0;*/
}

.pagination {
  @apply flex justify-between mt-10;
}

ul.pagination {
  @apply list-none ml-0;
}

.pagination--previous {
  @apply mr-auto;
}

.pagination--next {
  @apply ml-auto;
}

.pagination a {
  @apply border-b-0 text-base rounded-full py-2 px-4 border-b-0 no-underline hover:no-underline leading-8;
  color: var(--lychee-color);
  background-color: var(--spinach-color);
}