/* 
  Syntax Highlighting for Code Snippets

  https://www.bridgetownrb.com/docs/liquid/tags#stylesheets-for-syntax-highlighting

  Other styles available eg. https://github.com/jwarby/jekyll-pygments-themes
  To use another style, delete all styles in this file and replace them with
  the new styles. Or create your own!

*/

div.highlighter-rouge {
  @apply my-12 border border-t-4 rounded-b-3xl pr-4 overflow-auto;
  border-color: var(--grapefruit-color);
  background-color: var(--lychee-color);

  /* @apply mx-auto text-3xl relative md:max-w-xl p-6 font-serif my-12 border-t-4 border-green-400 bg-green-50 rounded-b-3xl; */
}

.highlight code {
  @apply no-underline;
}

.highlight .lineno { -webkit-user-select: none;-moz-user-select: none; -o-user-select: none; }
.lineno::-moz-selection { background-color: transparent; } /* Mozilla specific */
.lineno::selection { background-color: transparent; } /* Other major browsers */

pre.highlight  {
  @apply w-full overflow-auto p-6;
  /*border: 1px solid;*/
  /*border-color: var(--tangerine-color);*/
}

/* .highlight .hll { background-color: #49483e } */
.highlight .c { @apply text-gray-400; } /* Comment */
